import React from "react";
import "./styles.css";

const Benefits = () => {
  const lists = [
    {
      label:
        "A plataforma intermediar o agendamento de consultas entre voc e seus pacientes;",
    },
    {
      label: "Seu perfil aparecer para milhares de pacientes de sua regio;",
    },
    {
      label: "Todas as consultas sero particulares (sem convnio mdico);",
    },
    {
      label: "Um aplicativo/site de gesto de clnicas totalmente gratuito.",
    },
  ];

  return (
    <div className="benefits">
      <div className="container">
        <h1>Como proporcionaremos tais benefcios?</h1>
        <ul>
          {lists.map((list, index) => {
            return (
              <li className="benefit" key={index}>
                <div className="small-list-style"></div>
                {list.label}
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
};

export default Benefits;
