import React from "react";
import Benefits from "../../components/Benefits";
import Header from "../../components/Header";
import LeftSideBenefit from "../../components/LeftSideBenefit";
import Presentation from "../../components/Presentation";
import Title from "../../components/Title";
import "./styles.css";
import booking from "../../assets/images/booking.svg";
import checkbox from "../../assets/images/checkbox.svg";
import interview from "../../assets/images/interview.svg";
import wallet from "../../assets/images/wallet.svg";
import RightSideBenefit from "../../components/RightSideBenefit";
import Tax from "../../components/Tax";
import Refund from "../../components/Refund";
import Footer from "../../components/Footer";

const LandingPage = () => {
  return (
    <div className="landing-page">
      <Header />
      <Presentation />
      <Benefits />
      <Title />
      <LeftSideBenefit
        title="Agenda"
        image={booking}
        description="Aqui temos uma agenda que listar todos os compromissos do mdico de forma automtica ou manual!"
      />
      <RightSideBenefit
        title="Histrico de pacientes"
        image={interview}
        description="O histrico de todos os clientes ser guardado na nuvem e podem ser consultados a qualquer momento"
      />
      <LeftSideBenefit
        title="Histrico de consultas"
        image={checkbox}
        description="Informaes como preos, data e hora das consultas  importante. Por conta disso disponibilizamos o histrico de suas consultas."
      />
      <RightSideBenefit
        title="Sistema de pagamento"
        image={wallet}
        description="No Spital, utilizamos um sistema de pagamento desenvolvido pela Stripe. Com um clique voc consegue sacar seu dinheiro das consultas!"
      />
      <Tax />
      <Refund />
      <Footer />
    </div>
  );
};

export default LandingPage;
